import React from 'react';
import Swiper from 'react-id-swiper';
import 'swiper/css/swiper.css';
import '../app.css';

const FeedPanel = () => {

  return (
    <Swiper className="main-content">
      <div className="main-content-feed">Slide #1</div>
      <div className="main-content-feed">Slide #2</div>
      <div className="main-content-feed">Slide #3</div>
      <div className="main-content-feed">Slide #4</div>
      <div className="main-content-feed">Slide #5</div>
    </Swiper>
  )
};
export default FeedPanel;